import React, { useState } from 'react';
import Template from './Template';
import Client from 'rest_client/baseClient';
import { useNavigate } from 'react-router';
function Register() {
  const http = new Client();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [data, setData] = useState({
    username: '',
    email_address: '',
    password: '',
    agree_receive_emails: false,
  });
  const submit = async () => {
    setError('');
    setLoading(true);
    try {
      let res = await http.post('api/v1/auth/register', data);
      if (res && res.data.error_message) {
        setLoading(false);
        return setError(res.data.error_message);
      }
      navigate('/sign-up');
    } catch (err) {
      setLoading(false);
      if (err.error_message) {
        setError(err.error_message);
      } else {
        setError(err);
      }
    }
    setLoading(false);
  };
  const isDisabledButton = () => {
    return !data.username || !data.email_address || !data.password || !data.agree_receive_emails;
  };
  return (
    <Template
      title="Sign Up"
      submitLabel="Register"
      isLoading={loading}
      isDisabledButton={isDisabledButton()}
      onSubmit={submit}
      error={error}
      onClickGmail={() => {
        alert('gmail');
      }}
      onClickApple={() => {
        alert('apple');
      }}
      form={[
        {
          name: 'username',
          placeholder: 'Username',
          value: data.username,
          onChange: (e) => {
            setData({ ...data, username: e.target.value });
          },
        },
        {
          name: 'email',
          placeholder: 'Email',
          value: data.email,
          onChange: (e) => {
            setData({ ...data, email_address: e.target.value });
          },
        },
        {
          name: 'password',
          placeholder: 'Password',
          value: data.password,
          type: 'password',
          onChange: (e) => {
            setData({ ...data, password: e.target.value });
          },
        },
      ]}
      footer={{
        text: `Have an account?`,
        linkLabel: 'Sign In',
        to: '/sign-in',
      }}
      checkbox={{
        handleChange: (e) => {
          setData({ ...data, agree_receive_emails: e.target.checked });
        },
        label: `I agree to receive emails from SmartProp Trader & I've Read and accept the Terms & Conditions.`,
      }}
    />
  );
}

export default Register;
